@import '../sass/mixins/_mixins.sass';

.error-container {
  position: relative;
  text-align: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: $white;
  height: 100vh;
  
  .error-not-found {
    margin-top: 10.5em;
    position: fixed;
    text-align: center;
    top: 50%;
    left: 50%;
    //-webkit-transform: translate(-50%, -50%);
    //transform: translate(-50%, -50%);
    color: $white
  }
}

