@import '../sass/mixins/_mixins.sass';


.mobile__menu {
  // position: fixed;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 9.6rem 2rem 2.4rem;
  background-color: $card-d-2;
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 10;
  &-links {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  &-link {
    @include font(1.4rem, 1.71em, 600);
    color: $white;
    padding: 1.2rem 5.2rem;
    &:hover, &:active {
      border-radius: .4rem;
      background-color: $text-d-2;
    }
  }
}

.header {
  position: absolute;
  &-fixed {
    position: fixed;
    background-color: $socket-d; // #11151b;
  }

  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  @include lg {
    // display: none;
  }
  .container {
    @include lg {
      position: relative;
      z-index: 100;
    }
  }
  nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .nav-link {
      cursor: pointer;
      @include font(1.6rem, 1.75em, 600);
      color: $white;
      @include transition;
      &:hover, &:not(:last-child) {
        margin-right: 4.8rem;
      }
    }
  }
}

.menu__top {
  border-bottom: 1px solid $text-d-text-d-6;
  padding: 2rem 0;
  @include lg {
    padding: 1.6rem 0;
  }
}

.soc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  @include lg;

  .soc-link {
    display: flex;
    width: 4.8rem;
    height: 4.8rem;
    align-items: center;
    justify-content: center;
    @include lg {
      width: 2rem;
      height: 2rem;
      &:not(:last-child) {
        margin-right: 1.6rem;
      }
    }
    svg {
      path {
        @include transition;
      }
    }
    &:hover {
      svg {
        path {
          fill-opacity: 1;
        }
      }
    }
  }
}

.header {
  &-mmenu {
    display: flex;
    width: 2.4rem;
    height: 2.4rem;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
}


.header-info {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  @include lg {
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: relative;
  }
  &__wallet {
    margin-left: 3.2rem;
    @include lg {
      margin-left: 1.6rem;
    }
    .l-primary-button {
      @include lg {
        height: 4.4rem;
      }
    }
    .btn {
      @include lg {
        span {
          padding: .6rem  1.6rem;
          @include font(1.4rem, 1.71em, 600);
        }
      }
    }
  }
